* {
  box-sizing: border-box;
}

.img {
  width: 300px;
}

.info {
  width: 300px;
  display: flex;
  justify-content: space-between;
}

.info-item {
  text-align: center;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  margin-top: -5px;
  margin-left: -5px;
}

.photo-card {
  flex-basis: calc(100% / 4 - 10px);
  margin-top: 5px;
  margin-left: 5px;
}

.load-more {
  display: block;
  margin-top: 20px;
  align-items: center;
}

.show {
  opacity: 1;
}

.hide {
  opacity: 0;
}
